import { pathToBase64, base64ToPath } from '@/components/GRZX/js_sdk/gsq-image-tools/image-tools/index.js';
import listCell from '@/components/GRZX/mix-list-cell';
import {  
    mapState 
} from 'vuex'; 
export default{
	components: {
		listCell
	},
	data(){
		return{
			QQ:'2482549389',
			nickname:'',
			portrait:'',
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	onLoad(){
	},
	onShow(){
		this.loadData();
	},
	onNavigationBarButtonTap(e) {
		const index = e.index;

		if(index === 2){
			uni.navigateTo({
				url:'/pages/GRZX/set'
			})
		}
		if(index === 0){
			uni.scanCode({
				onlyFromCamera:true,
				success:function(res){
					void plus.runtime.openWeb(res.result,function(){
						//识别失败
					});
				}
			})
		}








		if(index === 1){
			uni.navigateTo({
				url:'/pages/GRZX/myNews'
			})
		}
	},
	methods:{
		loadData(){
			var that=this;
			var user=uni.getStorageSync('userInfo');
			uni.request({
				url:that.$GrzxInter.Interface.login.value,
				data:{
					phoneNumber:user.phoneNumber,
				},
				method:that.$GrzxInter.Interface.login.method,
				success(res) {
					console.log(res,'res')
					that.nickname=res.data.data.nickname;
					var base64=res.data.data.portrait;
					if(that.isBase64(base64)){
						base64ToPath(base64)
						  .then(path => {
						    that.portrait=path;
						  })
						  .catch(error => {
						    console.error(error)
						  })
					}else{
						that.portrait=base64;
					}
					console.log(that.portrait,"that.portrait")
				}
			})
		},
		orderClick(){
			uni.switchTab({
				url:'/pages/order/OrderList'
			})
		},
		navTo(url){
			uni.navigateTo({
				url
			})
			console.log(url)
		},
		//信息管理
		infoClick(){
			uni.navigateTo({
				url:'/pages/GRZX/infoList'
			})
		},
		// 投诉
		complaintClick(){
			uni.navigateTo({
				url:'/pages/GRZX/complaint'
			})  
		},
		feedbackClick(){
			uni.navigateTo({
				url:'/pages/GRZX/feedback'
			})  				
		},
		checkLogin(){
			// console.log(this.hasLogin,"6666")

			if(!this.hasLogin){
				uni.showToast({
					title : '请先登录',
					icon : 'none',
				})
				setTimeout(function(){
					uni.navigateTo({	
						//loginType=1,泉运登录界面
						//loginType=2,今点通登录界面
						//loginType=3,武夷股份登录界面
						url  : '/pages/GRZX/userLogin?loginType=1&&urlData=1'
					}) 
				},500);
			}else{
				uni.navigateTo({
					url :'/pages/GRZX/personal'
				})  
			}



















		},
		collectionClick(){
			// uni.navigateTo({
			// 	url:'/pages/GRZX/collection'
			// }) 
			uni.showToast({
				title:'正在测试中，敬请期待...',
				icon:'none'
			})
		},
		historyClick(){
			// uni.navigateTo({
			// 	url:'/pages/GRZX/history'
			// }) 
			uni.showToast({
				title:'正在测试中，敬请期待...',
				icon:'none'
			})
		},
		scanClick(){
			uni.showToast({
				title : '扫一扫',
				icon : 'none',
			})
		},
		QQClick(){
			plus.runtime.openURL('mqq://im/chat?chat_type=wpa&uin=' + this.QQ + '&version=1&src_type=web ');
		},
		//------------判断是否为base64格式-----------
		isBase64:function(str) {
		    if (str ==='' || str.trim() ===''){ return false; }
		    try {
		        return btoa(atob(str)) == str;
		    } catch (err) {
		        return false;
		    }
		},
	}
	
}
